.App {
  font-family: "Arimo", sans-serif;
  text-align: left;
  line-height: 2;
}

.text-center {
  text-align: center;
}

.container {
  padding: 0 15px;
  max-width: 980px;
  margin: 0 auto;
}

.row {
  margin-left: -15px;
  margin-right: -15px;
}

.column {
  padding: 0 15px;
}

h4 {
  text-transform: uppercase;
  text-align: left;
}

.formControl {
  width: 100%;
  height: 40px;
  border: 1px solid #e9e9e9;
  margin-bottom: 15px;
  text-align: center;
  outline: none;
  box-shadow: none;
}

.btn-submit {
  background: blue;
  border: none;
  color: #fff;
  width: 100%;
  padding: 10px;
  font-size: 18px;
  text-transform: uppercase;
  cursor: pointer;
}

.fa-star {
  width: 15px;
  height: 15px;
  display: inline-block;
  background: goldenrod;
  border: 1px solid #ddd;
  margin-right: 15px;
}
svg {
  cursor: pointer;
}
.star-filled {
  fill: gold;
}

.star-blank {
  fill: grey;
}

.fa-star:last-child {
  margin-right: 0;
}

.fa-star.inactive {
  background: red;
}

.ratingsHolder {
  text-align: center;
}
